/* Global Styles */
body {
    margin: 0;
    font-family: Arial, sans-serif;
    color: #333;
  }
  
  .App {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
  }
  
  /* Header Styles */
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    background-color: #f8f8f8;
  }
  
  header .logo {
    font-size: 24px;
    font-weight: bold;
  }
  
  header nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  header nav ul li {
    margin-left: 20px;
  }
  
  header nav ul li a {
    text-decoration: none;
    color: #333;
  }
  
  /* Hero Section Styles */
  .hero {
    text-align: center;
    padding: 100px 20px;
    background-color: #ff4d4d;
    color: white;
    flex: 1;
  }
  
  .hero h1 {
    font-size: 48px;
    margin-bottom: 20px;
  }
  
  .hero p {
    font-size: 18px;
    margin-bottom: 40px;
  }
  
  .buttons .btn {
    padding: 10px 20px;
    margin: 0 10px;
    text-decoration: none;
    color: white;
    background-color: #333;
    border-radius: 5px;
  }
  
  .buttons .btn.donate {
    background-color: #ff4d4d;
    border: 2px solid white;
  }
  
  .buttons .btn.sign-in {
    background-color: white;
    color: #333;
  }
  
  /* Footer Styles */
  footer {
    text-align: center;
    padding: 20px;
    background-color: #333;
    color: white;
  }
  
  footer .social-icons {
    margin-top: 10px;
  }
  
  footer .social-icons a {
    margin: 0 10px;
    color: white;
    text-decoration: none;
  }